import React, { useEffect, useState, useCallback } from 'react'
import { Calendar, Button, Popover } from 'antd'
import { useContentful } from 'src/hooks/useContentful'
import { GET_BIRTHDAYS } from '../../constants/graphQL'
import { IBirthdayItem } from 'src/domain/types/Birthday'
import moment, { Moment } from 'moment'
import './CalendarPage.scss'
import 'antd/dist/antd.css'
import { getBirthdaysData } from '../../api/utils/getBirthdaysData'

moment.updateLocale('en', { week: { dow: 1 } })

export const CalendarPage = () => {

  const { getListData } = getBirthdaysData()

  const content = (value: Moment): JSX.Element => {
    const listData: IBirthdayItem[] = getListData(value)
    return (
      <ul className='events'>
        {listData.length
          ? listData.map((item: IBirthdayItem) => (
              <div>
                <li key={item.name}>
                  <p>{item.name}</p>
                  <p>{item.name}</p>
                  <p>{item.name}</p>
                  <p>{item.name}</p>
                  <p>{item.name}</p>
                  <p>{item.name}</p>
                  <p>{item.name}</p>
                  <p>{item.name}</p>
                  <p>{item.name}</p>
                  <div className='nav__icon'></div>
                </li>
              </div>
            ))
          : ''}
      </ul>
    )
  }

  const dateCellRender = (value: Moment): JSX.Element => {
    const listData: IBirthdayItem[] = getListData(value)
    return (
      <Popover placement='rightTop' content={content(value)} trigger='click'>
        <ul className='events'>
          {listData.length
            ? listData.map((item: IBirthdayItem) => (
                <div>
                  <li key={item.name}>
                    <p>{item.name}</p>
                    <div className='nav__icon'></div>
                  </li>
                </div>
              ))
            : ''}
        </ul>
      </Popover>
    )
  }

  return (
    <div className='calendar'>
      <Calendar dateCellRender={dateCellRender} className='calendar__item' />
    </div>
  )
}
